.nav-btns__tel{
    position: absolute;
    top: 0;
    right: 0;
    background: rgba(255, 255, 255, 0.35);
    backdrop-filter: blur(5px);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    height: 100%;
    width: 15rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: 4rem;
    .btn-tel{
        font-weight: $bold;
        font-size: 1.8rem;
    }
    .btn{
        @include btn;
    }
}